.backdrop {
  height: 100vh;
  width: 100vw;
  perspective: 1600px;
  z-index: 7;
  opacity: 1;
  visibility: visible;
  background-color: rgba(0, 0, 0, .2);
  transition: opacity .3s linear, visibility .3s linear;
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.backdrop.is-hidden .modal {
  transition: transform .5s;
  transform: translate(-50%, -50%)scale(.5)rotateY(180deg);
}

.modal {
  width: 290px;
  max-width: 90vw;
  background-color: var(--pink-color);
  border-radius: 20px;
  padding: 10px;
  transition: transform .3s linear;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1)rotateY(0deg);
